$color: #fffdc5;
$h3color: #f7931e;

body{
  background-color: $color;
}
h1 {
  display: none;
}
h3 {
  font-size: 20px;
  color: $h3color;
  font-weight: 400;
  z-index: 1000;
  background-color: $color;
  position: relative;
  width: 450px;
  margin: 0 auto;
}
.icon1 {
  display: block;
  width: 125px;
  height: auto;
  top: 30px;
  right: 300px;
  position: absolute;
  z-index: 300;

}
.logo_bear{
  width: 236px;
  height: auto;
  top: 160px;
  right: 45px;
  position: absolute;
  z-index: 300;
}
.icon2 {
