.container{
  margin: 0 auto;
  width: 960px;
  text-align: center;
}
.logo {
  display: block;
  margin: 100px auto 0px;
  position: relative;
  background-color: $color;
  z-index: 400;
}
.company_name {
  display: block;
  margin: 64px auto 22px;
  position: relative;
  background-color: $color;
  z-index: 400;
}
