.footer {
  margin: 0 auto;
  width: 90%;
  height: 1px;
  background-image: linear-gradient(to right, #ccc 0%, #ccc 50%, transparent 80%);
  background-size: 8px 1px;
  background-repeat: repeat-x;
  position: relative;
  top:-15px;
  z-index: 200;
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  transition: all 0.5s;
  &:hover {
    width: 200px;
    background-color: $color;
  }
}
